export default {
	props: ['design','background','title'],
	data() {
		return {
			indicatorDots:true,
			autoplay:false,
			duration:300
		}
	},
	methods:{
		showimg(current) {
			
			let urls = this.design.map(item => {
				return item.image
			})
		
			uni.previewImage({
				current,
				urls
			})
		}
	}
}
